#aboutme {
  height: 35%;
  flex-direction: column;
  justify-content: space-around;
}

.aboutme__container {
  border: solid 2px #2dc4e7;
  font-size: 19px;
  padding: 20px;
  place-content: space-around;
  display: grid;
  text-align: justify;
  border-radius: 10px;
  height: 50%;
}

@media screen and (max-width: 1000px) {
  #aboutme {
    height: 25%;
  }
}

@media screen and (max-width: 590px) {
  #aboutme {
    height: 30%;
  }

  .aboutme__container {
    height: auto;
  }
}
