footer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
}

.footer-iconos {
  display: flex;
  gap: 15px;
}

.footer-boton {
  padding: 15px;
  font-size: 15px;
  display: flex;
  border: none;
  background-color: #f0f0f0;
  border-radius: 25px;
  transition: all 0.3s;
  cursor: pointer;
}

.footer-boton:nth-child(1):hover {
  background: radial-gradient(
    circle at 30% 107%,
    #fdf497 0%,
    #fdf497 5%,
    #fd5949 45%,
    #d6249f 60%,
    #285aeb 90%
  );
  color: white;
}

.footer-boton:nth-child(2):hover {
  background-color: #505257;
  color: white;
}

.footer-boton:nth-child(3):hover {
  background-color: #9147ff;
  color: white;
}
