header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
}

.header-img {
  height: 100px;
}

ul {
  display: flex;
  gap: 15px;
  list-style: none;
}

ul li {
  font-weight: bold;
  transition: all 0.2s;
}

ul li:hover {
  transform: translateY(5px);
}

.botones-header {
  display: flex;
  gap: 13px;
  align-items: center;
}

.boton__contacto,
.boton__cerrar {
  padding: 8px 15px;
  font-weight: bolder;
  letter-spacing: 0.5px;
  border: none;
  border-radius: 8px;
  background-color: #c4c4c4;
  transition: all 0.2s;
  cursor: pointer;
}

.boton__contacto:hover,
.boton__cerrar:hover {
  background-color: #dedddd;
}

.dark-mod-boton,
.boton-idioma {
  display: flex;
  border: solid #c4c4c4 1px;
  border-radius: 5px;
  padding: 8px;
  background-color: transparent;
  /* transition: all 0.7s!important; */
  transition: all 0.3s;
  cursor: pointer;
}

.dark-mod-boton:hover,
.boton-idioma:hover {
  /* transform: rotate(360deg); */
  color: red !important;
}

.hamburguesa {
  display: flex;
  flex-direction: column;
  gap: 5px;
  cursor: pointer;
}

.linea {
  width: 45px;
  height: 5px;
  transition: all 0.3s ease-in-out;
  background-color: black;
  border-radius: 5px;
}

.hamburguesa--show .linea:nth-child(1) {
  transform: translateY(5px) rotate(45deg);
}

.hamburguesa--show .linea:nth-child(2) {
  opacity: 0;
}

.hamburguesa--show .linea:nth-child(3) {
  transform: translateY(-15px) rotate(-45deg);
}

@media screen and (min-width: 769px) {
  .hamburguesa {
    display: none;
  }
}

@media screen and (max-width: 768px) {
  .modal-container {
    width: 50%;
  }

  nav {
    display: none;
  }

  .boton__contacto,
  .boton-idioma,
  .dark-mod-boton {
    display: none;
  }

  nav.show-nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
    animation: slide-in 0.5s ease-in-out;
  }

  button.show-btn {
    display: flex;
    animation: slide-in 0.5s ease-in-out;
  }
}

@media screen and (max-width: 590px) {
  .modal-container {
    width: 90%;
    height: 55%;
  }
}
