#inicio {
  height: 85vh;
  justify-content: space-between;
}

.inicio__container {
  display: grid;
  gap: 20px;
}

.inicio__text {
  text-align: start;
}

.inicio__titulo {
  display: flex;
  justify-content: space-between;
  font-size: 50px;
  font-family: "Shantell Sans", sans-serif;
}

.inicio__subtitulo {
  font-size: 30px;
}

.inicio__boton {
  background-color: #2dc4e7;
  padding: 15px;
  font-weight: bold;
  font-size: 20px;
  border: solid black;
  border-radius: 15px;
  transition: all 0.2s;
  cursor: pointer;
}

.inicio__boton:hover {
  background-color: #81def3;
}

@media screen and (max-width: 650px) {
  #inicio {
    flex-direction: column;
    justify-content: space-evenly;
  }
}
